import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get(`/api/teachers/assessment/teacher/${params.teacher_id}`, {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/teachers/assessment/${id}`);
	}

	update(data) {
		return axios.put(`/api/teachers/assessment/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/teachers/assessment' ,data);
	}

	delete(id) {
		return axios.delete(`/api/teachers/assessment/${id}`)
	}
}