import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get(`/api/staffs/offdays/list/${params.staff_id}`, {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/staffs/offdays/${id}`);
	}

	update(data) {
		return axios.put(`/api/staffs/offdays/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/staffs/offdays' ,data);
	}

	delete(id) {
		return axios.delete(`/api/staffs/offdays/${id}`)
	}
}