import { observable, action, set, get, computed, autorun, runInAction, toJS } from 'mobx';
import axios from 'axios';
import lodash from 'lodash';

export default class UserStore {
	@observable user;

	constructor() {
		if(window.localStorage['skyschool_active_user']){
			let user = JSON.parse(window.localStorage['skyschool_active_user']);
			this.user = user;
		} else {
			this.user = {email: null};
		}

		this.disposer = autorun( ()=>{
			window.localStorage['skyschool_active_user'] = JSON.stringify(toJS(this.user));
		} )
	}

	get() {
		return axios.get('/api/user').then( response => {
			runInAction( () => set(this.user, response.data) );
			return response.data;
		}, error => {
			if(error.response) {
				this.user = {email: null};
				window.localStorage['skyschool_active_user'] = JSON.stringify(toJS(this.user));
				throw error.response;
			} else {
				if(window.localStorage['skyschool_active_user']) {
					let user = JSON.parse(window.localStorage['skyschool_active_user']);
					if(user.email) {
						set(this.user, user);
						return this.user;
					} else {
						throw error;
					}
				}
				else 
					throw error;
			}
		})
	}

	@action
	setUser(user) {
		set(this.user,user);
	}

	getUser() {
		return get(this.user,'email');
	}

	@computed
	get email() {
		return get(this.user.email);
	}
}

const user = new UserStore();