import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get('/api/teachers/duty', {params: {...params}});
	}

	get(dutyId) {
		return axios.get(`/api/teachers/duty/${dutyId}`);
	}

	update(data) {
		return axios.put(`/api/teachers/duty/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/teachers/duty' ,data);
	}

	delete(dutyId) {
		return axios.delete(`/api/teachers/duty/${dutyId}`);
	}
}