import UserStore from './user';

import StudentApi from './studentApi';
import ReportApi from './reportApi';
import ClassesApi from './classesApi';
import SubjectApi from './subjectApi';
import SubjectGroupApi from './subjectGroupApi';
import ExamGroupApi from './examGroupApi';
import ExamApi from './examApi';
import RecordApi from './recordApi';
import PerformanceApi from './performanceApi';
import AnalysisApi from './analysisApi';
import {TeacherApi, TeacherDutyApi, TeacherAssessmentApi} from './teacher';
import {StaffApi, StaffOffdayApi} from './staff';

import generateStore from './generateStore';

export const userStore = new UserStore();
export const studentStore = new generateStore({api: new StudentApi(), storeName: 'students', key: 'id', index: 'class_id', backgroundSync: true, queueName: 'students-queue'});
export const reportStore = new generateStore({api: new ReportApi(), storeName: 'reports', key: 'id', index: 'student_id', backgroundSync: true, queueName: 'reports-queue'});
export const classesStore = new generateStore({api: new ClassesApi(), storeName: 'classes', key: 'id'});
export const subjectStore = new generateStore({api: new SubjectApi(), storeName: 'subjects', key: 'id'});
export const subjectGroupStore = new generateStore({api: new SubjectGroupApi(), storeName: 'subject-groups', key: 'id'});
export const examGroupStore = new generateStore({api: new ExamGroupApi(), storeName: 'examGroups', key: 'id'});
export const examStore = new generateStore({api: new ExamApi(), storeName: 'exams', key: 'id', index: 'exam_group_id'});
export const recordStore = new generateStore({api: new RecordApi(), storeName: 'records', key: ['exam_id', 'student_id'], index: 'exam_id', backgroundSync: true, queueName: 'records-queue'});
export const performanceStore = new generateStore({api: new PerformanceApi(), storeName: 'performances', key: 'id', index: 'class_id'});
export const analysisStore = new generateStore({api: new AnalysisApi(), storeName: 'analyses', key: 'id', index: 'class_id'});
export const teacherStore = new generateStore({api: new TeacherApi(), storeName: 'teachers', key: 'id', backgroundSync: true, queueName: 'teachers-queue'});
export const teacherDutyStore = new generateStore({api: new TeacherDutyApi(), storeName: 'teacher-duties', key: 'id', backgroundSync: true, queueName: 'teacher-duties-queue'});
export const teacherAssessmentStore = new generateStore({api: new TeacherAssessmentApi(), storeName: 'teacher-assessments', key: 'id', index: 'teacher_id', backgroundSync: true, queueName: 'teacher-assessments-queue'});
export const staffStore = new generateStore({api: new StaffApi(), storeName: 'staffs', key: 'id', backgroundSync: true, queueName: 'staffs-queue'});
export const staffOffdayStore = new generateStore({api: new StaffOffdayApi(), storeName: 'staff-offdays', key: 'id', index: 'staff_id', backgroundSync: true, queueName: 'staff-offdays-queue'});
