import idb from "../libs/idb-promised"

window.idb = idb;

function createStore(upgradeDb, storeName, keyPath, index) {
  if (!upgradeDb.objectStoreNames.contains(storeName)) {
    const store = upgradeDb.createObjectStore(storeName, {keyPath});
    if(index)
      store.createIndex(index, index);
  }
}

function createIndexedDB() {
  if (!('indexedDB' in window)) {return null;}
  return idb.open('skyschool', 1, function(upgradeDb) {
    console.log('creating');
    createStore(upgradeDb, 'students', 'id', 'class_id');
    createStore(upgradeDb, 'reports', 'id', 'student_id');
    createStore(upgradeDb, 'classes', 'id');
    createStore(upgradeDb, 'subjects', 'id');
    createStore(upgradeDb, 'examGroups', 'id');
    createStore(upgradeDb, 'exams', 'id', 'exam_group_id');
    createStore(upgradeDb, 'records', ['exam_id', 'student_id'], 'exam_id');
    createStore(upgradeDb, 'performances', 'id', 'class_id');
    createStore(upgradeDb, 'analyses', 'id', 'class_id');
    createStore(upgradeDb, 'teachers', 'id');
    createStore(upgradeDb, 'teacher-duties', 'id');
    createStore(upgradeDb, 'teacher-assessments', 'id', 'teacher_id');
    createStore(upgradeDb, 'staffs', 'id');
    createStore(upgradeDb, 'staff-offdays', 'id', 'staff_id');
  });
}

const dbPromise = createIndexedDB();
export default dbPromise;
