import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get('/api/examination', {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/examination/${id}`);
	}

	update(data) {
		return axios.put(`/api/examination/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/examination' ,data);
	}

	delete(id) {
		return axios.delete(`/api/examination/${id}`)
	}
}