import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get(`/api/reports/student/${params.student_id}`, {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/reports/${id}`);
	}

	update(data) {
		return axios.put(`/api/reports/${data.id}`, data);
	}

	create(data) {
		return axios.post('/api/reports' ,data);
	}

	delete(id) {
		return axios.delete(`/api/reports/${id}`);
	}
}