import React, {Component} from 'react'
import {Switch, withRouter} from 'react-router-dom'
import { TransitionGroup } from "react-transition-group";
import {SlideVertical} from './transitions'
import './animated-switch.css';

const urlPath = [];

class AnimatedSwitch extends Component{
  constructor(props) {
    super(props);

    this.key = this.props.location.key;
    this.index = urlPath.push(this.props.match.url) - 1;
    this.url = this.props.location.pathname;
  }
  componentWillUnmount() {
    let index = urlPath.indexOf(this.props.match.url);
    urlPath.splice(index,1);
  }
  render(){
    let changedPath = this.props.location.pathname.substr(this.props.match.url.length);
    let childPath = urlPath[this.index + 1];
    if(childPath){
      let childMatch = childPath.substr(this.props.match.url.length);
      if(!changedPath.startsWith(childMatch)){
        this.url = this.props.location.pathname;
        this.key = this.props.location.key;
      }
    }else if(this.url != this.props.location.pathname) {
      this.url = this.props.location.pathname;
      this.key = this.props.location.key;
    }
    

    return <TransitionGroup appear={true} className='animated-switch'>
        <SlideVertical key={this.key}>  
          <Switch location={this.props.location}>
            {this.props.children}
          </Switch>
        </SlideVertical>
      </TransitionGroup>
    
  }
}

export default withRouter(AnimatedSwitch);
