import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get('/api/staffs', {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/staffs/${id}`);
	}

	update(data) {
		return axios.put(`/api/staffs/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/staffs' ,data);
	}

	delete(id) {
		return axios.delete(`/api/staffs/${id}`)
	}
}