import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get('/api/teachers', {params: {...params}});
	}

	get(id) {
		return axios.get(`/api/teachers/${id}`);
	}

	update(data) {
		return axios.put(`/api/teachers/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/teachers' ,data);
	}

	delete(id) {
		return axios.delete(`/api/teachers/${id}`)
	}
}