import { observable, action, set, get, computed, runInAction } from 'mobx'
import idb from "../libs/idb-promised"
import dbPromise from './db';
import ShowNotification from '../components/ShowNotification';


const responseDB = idb.open('response-cache', 1, function(upgradeDb) {
	if (!upgradeDb.objectStoreNames.contains('responses')) {
    const responses = upgradeDb.createObjectStore('responses', {keyPath: 'id', autoIncrement: true});
    responses.createIndex('store', 'store');
  }
  if (!upgradeDb.objectStoreNames.contains('items')) {
    const items = upgradeDb.createObjectStore('items', {keyPath: ['__meta_store','__meta_id']});
    items.createIndex('__meta_store', '__meta_store');
  }
});

	export default function store(config) {
		this.config = config;
		this.api = config.api;
		if(config.backgroundSync){
			this.syncQueueDBPromise = idb.open(config.syncDB || 'workbox-background-sync', config.syncDBVersion || 3)  || null;
		}

		if(Array.isArray(config.key)) {
			this.getKey = this.getKeyArray;
			this.hasKey = this.hasKeyArray;
			this.compareItem = this.compareItemArrayKey;
			this.compareItemWithKey = this.compareItemWithArrayKey;
			this.compareKey = this.compareKeyArrayKey;
			this.insertDummyKey = this.insertDummyKeyArray;
			this.isDummy = this.isDummyArray;
			this.getItemFromKey = this.getItemFromKeyArray;
		} else {
			this.getKey = this.getKeySingle;
			this.hasKey = this.hasKeySingle;
			this.compareItem = this.compareItemSingleKey;
			this.compareItemWithKey = this.compareItemWithSingleKey;
			this.compareKey = this.compareKeySingleKey;
			this.insertDummyKey = this.insertDummyKeySingle;
			this.isDummy = this.isDummySingle;
			this.getItemFromKey = this.getItemFromKeySingle;
		}

		if(config.filter) {
			if(Array.isArray(config.filter)) {
				this.satisfyFilter = this.satisfyArrayFilter;
			} else {
				this.satisfyFilter = this.satisfySingleFilter;
			}
		} else {
			this.satisfyFilter = () => true;
		}

		if(config.listField) {
			if(config.listField.split('.').length > 1){
				this.listField = config.listField.split('.');
				this.listResponseData = this.listResponseDataNestedMultiple;
				this.setListResponseData = this.setListResponseDataNestedMultiple;
			}
			else{
				this.listField = config.listField;
				this.listResponseData = this.listResponseDataNested;
				this.setListResponseData = this.setListResponseDataNested;
			}
		} else {
			this.listResponseData = (response) => response;
			this.setListResponseData = (response, data, override = true) => override ? data : [...data, ...response];
			this.setGetResponseData = (response, data) => data;
		}

		if(config.getField) {
			if(config.getField.split('.').length > 1){
				this.getField = config.getField.split('.');
				this.getResponseData = this.getResponseDataNestedMultiple;
				this.setGetResponseData = this.setGetResponseDataNestedMultiple;
			}
			else{
				this.getField = config.getField;
				this.getResponseData = this.getResponseDataNested;
				this.setGetResponseData = this.setGetResponseDataNested;
			}
		} else {
			this.getResponseData = (response) => response;
		}

	}

	store.prototype.get = async function(itemId, cache) {
		let itemCache = await this.getItem(itemId);
		let keyItem = this.getItemFromKey(itemId);
		if(this.isDummy(keyItem)) {
			return this.setGetResponseData({},itemCache);
		} else if (cache && itemCache) {
			this.getFetch(itemId);
			return this.setGetResponseData({},itemCache);
		} else {
			try {
				let response = await this.getFetch(itemId);
				return response;
			} catch (error) {
				if(!error.response) {
					return this.setGetResponseData({},itemCache);
				}
				throw error.response.data;
			}
		}
	};

	store.prototype.getFetch = async function(itemId) {
		let itemCache = await this.getItem(itemId);
		let response = await this.api.get(itemId);
		let item = this.getResponseData(response.data);
		if(itemCache && itemCache.__meta_pending){
			item = {...response.data, ...itemCache, __meta_prev: item};
			await this.saveItems([item]);
		} else {
			await this.saveItems([item])
		}
		return this.setGetResponseData(response.data, item);
	}

	store.prototype.list = async function(params, cache=false, unsaved=false) {
		let unsavedItems = unsaved ? await this.listUnsaved(params) : [];
		let cachedResponse = await this.findListResponse(params);
		if( cache && cachedResponse && !cachedResponse.update && this.listResponseData(cachedResponse.response).length) {
			this.listFetch(params);
			let response = await this.restoreListResponse(cachedResponse);
			return this.setListResponseData(response, unsavedItems, false);
		} else {
			try {
				let response = await this.listFetch(params);
				return this.setListResponseData(response, unsavedItems, false);
			} catch (error) {
				if(!error.response && cachedResponse) {
					let response = await this.restoreListResponse(cachedResponse);
					return this.setListResponseData(response, unsavedItems, false);
				}
				throw (error.response ? error.response.data : error);
			}
		}
	}

	store.prototype.listFetch = async function(params) {
		let response = await this.api.list(params);
		response = await this.cacheListResponse(response.data, params);
		return response;
	}

	store.prototype.cacheListResponse = async function(response,params) {
		let items = this.listResponseData(response);
		let itemsKey = []
		for(let item of items) {
			itemsKey.push(this.getKey(item))
		}

		response = this.setListResponseData(response, itemsKey);
		let cache = {response, params, store: this.config.storeName, timestamp: Date.now()};
		let prevResponse = await this.findListResponse(params);
		if(prevResponse) {
			cache.id = prevResponse.id;  //reuse id;
			let prevItemsKey = this.listResponseData(prevResponse.response);
			let key;
			for(let prevItemKey of prevItemsKey) {
				key = itemsKey.find( i => this.compareKey(i, prevItemKey));
				if(!key) {
					await this.deleteItem(prevItemKey, prevResponse.id);
					await this.setForceListUpdate(prevResponse.id);
				}
			}
			if(itemsKey.length !== prevItemsKey.length)
				await this.setForceListUpdate(prevResponse.id);
		}
		let responseId = await responseDB.then( async (db) => {
	    const tx = db.transaction('responses', 'readwrite');
	    const store = tx.objectStore('responses');
	    return store.put(cache);
	  });

	  items = await this.saveItems(items, false, responseId);
	  return this.setListResponseData(response, items);
	}

	store.prototype.getCacheListResponse = async function(params) {
		let response = await this.findListResponse(params);
		if(!response)
			return null;
		let itemsKey = this.listResponseData(response.response);
		let items = await responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readonly');
	    const store = tx.objectStore('items');
	    let items = [];
	    for(let key of itemsKey) {
	    	let itm = await store.get(this.getKeyStoreFromKey(key));
	    	if(itm)
	    		items.push(itm);
	    }
	    return items;
	  });
	  return this.setListResponseData(response, items);
	}

	store.prototype.listUnsaved = async function(params = {}) {
		return responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readonly');
	    const store = tx.objectStore('items').index('__meta_store');
	    let items = [];
	    let cursor = await store.openCursor(this.config.storeName);
	    while(cursor) {
	    	if(this.isDummy(cursor.value) && Object.keys(params).reduce((b,param) => ( b && (params[param] == cursor.value[param] || !(param in cursor.value))), true)) {
	    		items.push(cursor.value);
	    	}
	    	cursor = await cursor.continue()
	    }
	    return items;
	  });
	}

	store.prototype.getItem = async function(itemId) {
		itemId = this.formatKey(itemId);
		return responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readonly');
	    const store = tx.objectStore('items');
	    return store.get(this.getKeyStoreFromKey(itemId));
	  });
	}


	store.prototype.create = async function(item) {
		if(this.config.backgroundSync) {
			if(this.hasKey(item)) {
				await this.deleteSync(this.getKey(item));
			} else {
				item = this.insertDummyKey(item);
			}
		}
		try{
			let response = await this.api.create(item);
			await this.setForceListUpdate();
			item = this.getResponseData(response.data);
			await this.saveItem(item);
			return response.data;
		} catch(error) {
			if(this.config.backgroundSync) {
				if(error.response){
					if(item.__meta_pending){
						item.__meta_error = error.response.data;
						this.saveItem(item);
					}
					throw error.response.data;
				} else {
					let newItem = await this.cacheItemCreate(item, error);
					ShowNotification("You are offline, Request has been saved and will be sent once connection has resumed", "success", "nc-check-2");
					return this.setGetResponseData({},newItem);
				}
			} else {
				throw (error.response ? error.response.data : error);
			}
			
		}
	}

	store.prototype.update = async function(item) {
		if(this.config.backgroundSync)
			this.deleteSync(this.getKey(item));
		try {
			let response = await this.api.update(item);
			item = this.getResponseData(response.data);
			console.log(item);
			await this.saveItem(item)
			console.log(item);
			return response.data;
		} catch (error) {
			if(this.config.backgroundSync) {
				if(error.response){
					if(item.__meta_pending){
						item.__meta_error = error.response.data;
						this.saveItem(item);
					}
					throw error.response.data;
				} else {
					let newItem = await this.cacheItemUpdate(item, error);
					ShowNotification("You are offline, Request has been saved and will be sent once connection has resumed", "success", "nc-check-2");
					return this.setGetResponseData({},newItem);
				}
			} else {
				throw (error.response ? error.response.data : error);
			}
		}
	}

	store.prototype.delete = async function(itemId) {
		try {
			if(this.config.backgroundSync)
				await this.deleteSync(itemId);
			await this.api.delete(itemId);
			await this.deleteItem(itemId);
		} catch (error) {
			if(error.response)
				throw error.response.data;
			throw error;
		}
		
	}

	store.prototype.cancel = async function(itemId) {
		await this.deleteSync(itemId);
		let item = await this.getItem(itemId);
		if(item && item.__meta_pending) {
			if(this.isDummy(item)) {
				await this.deleteItem(itemId);
			} else {
				if(item.__meta_prev){
					await this.saveItem(item.__meta_prev);
				} else {
					item = await this.api.get(itemId);
					await this.saveItem(item);
				}
			}
		}
	}

	store.prototype.updateItem = async function(item, override=false) {
		return this.saveItem(item, override);
	}

	store.prototype.saveItemDataLocally = async function(items, override=true) {
	  if (!('indexedDB' in window)) {return null;}
	  let itemCache;
	  return  dbPromise.then( async (db) => {
	    const tx = db.transaction(this.config.storeName, 'readwrite');
	    const store = tx.objectStore(this.config.storeName);
	    for(let item of items) {
	    	itemCache = await store.get(this.getKey(item));
	    	if(override){
	    		await store.put(item);
	    	} else if(itemCache) {
	    		if(itemCache.pending)
	    			await store.put({...item, ...itemCache});
	    		else
	    			await store.put({...itemCache, ...item});
	    	} else {
	    		await store.put(item);
	    	}
	    }
	  });
	}

	store.prototype.saveItem = async function(item, override) {
		return this.saveItems([item], override);
	}

	store.prototype.saveItems = async function(items, override=true, responseId=null) {
	  if (!('indexedDB' in window)) {return null;}
	  let savedItems = [];
	  return  responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readwrite');
	    const store = tx.objectStore('items');
	    let saveItem;
	    for(let item of items) {
	    	let itemCache = await store.get(this.getKeyStore(item));
	    	if(responseId) {
	    		if(!itemCache || !itemCache.__meta_res_ids) {
	    			item.__meta_res_ids = [responseId];
	    		} else {
	    			item.__meta_res_ids = [...itemCache.__meta_res_ids.filter(id => id != responseId),responseId];
	    		}
	    	}
	    	if(override){
	    		saveItem = {...item, __meta_store: this.config.storeName, __meta_id: this.getKey(item)};
	    	} else {
	    		if(itemCache && itemCache.__meta_pending) {
	    			saveItem = {...item, ...itemCache};
	    		} else {
	    			saveItem = {...item, __meta_store: this.config.storeName, __meta_id: this.getKey(item)};
	    		}
	    	}
	    	await store.put(saveItem);
	    	savedItems.push(saveItem);
	    }
	    return savedItems;
	  });
	}

	store.prototype.getLocalItems = async function(params = {}, unsaved=false) {
		if (!('indexedDB' in window)) {return null;}
	  return dbPromise.then( async (db) => {
	    const tx = db.transaction(this.config.storeName, 'readonly');
	    const store = tx.objectStore(this.config.storeName);
	    let cursor;
	    if(this.config.index) {
	    	let indexValue = params[this.config.index] || '';
	    	indexValue = parseInt(indexValue) || indexValue;
	    	cursor = await store.index(this.config.index).openCursor(indexValue);
	    } else {
	    	cursor = await store.openCursor();
	    }
	    let items = [];
    	while(cursor && this.satisfyFilter(cursor.value, params)) {
    		if(this.isDummy(cursor.value)){
    			if(unsaved){
    				items.push(cursor.value);
    			}
    		} else {
    			items.push(cursor.value);
    		}
    		cursor = await cursor.continue();
    	}
    	return items;
	  });
	}

	store.prototype.restoreListResponse = async function (response) {
		let itemsKey = this.listResponseData(response.response);
		let items = await responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readonly');
	    const store = tx.objectStore('items');
	    let items = [];
	    for(let key of itemsKey) {
	    	let itm = await store.get(this.getKeyStoreFromKey(key));
	    	if(itm)
	    		items.push(itm);
	    }
	    return items;
	  });
	  return this.setListResponseData(response, items);
	}

	store.prototype.getLocalItem = async function(itemId) {
		if (!('indexedDB' in window)) {return null;}
		itemId = this.formatKey(itemId);
	  return dbPromise.then(db => {
	    const tx = db.transaction(this.config.storeName, 'readonly');
	    const store = tx.objectStore(this.config.storeName);
	    return store.get(itemId);
	  });
	}

	store.prototype.deleteLocalItem = async function(itemId) {
		if (!('indexedDB' in window)) {return null;}
		itemId = this.formatKey(itemId);
	  return dbPromise.then(db => {
	    const tx = db.transaction(this.config.storeName, 'readwrite');
	    const store = tx.objectStore(this.config.storeName);
	    return store.delete(itemId);
	  });
	}

	store.prototype.deleteItem = async function(itemId, responseId) {
		if (!('indexedDB' in window)) {return null;}
		itemId = this.formatKey(itemId);
		itemId = this.getKeyStoreFromKey(itemId);
	  return responseDB.then( async (db) => {
	    const tx = db.transaction('items', 'readwrite');
	    const store = tx.objectStore('items');
	    if(!responseId){
	    	return store.delete(itemId);
	    }
	    else {
	    	let item = await store.get(itemId);
	    	if(!item)
	    		return;
	    	if(!item.__meta_res_ids || item.__meta_res_ids.length == 0) 
	    		return store.delete(itemId);
	    	else {
	    		let i = item.__meta_res_ids.indexOf(responseId);
	    		if(i >= 0) {
	    			item.__meta_res_ids.splice(i, 1);
	    			if(item.__meta_res_ids.length == 0)
	    				return store.delete(itemId);
	    			else{
	    				await store.put(item);
	    				this.validateItem(item);
	    			}
	    		}
	    	}
	    }
	  });
	}

	store.prototype.validateItem = async function(item) {
		try {
			await this.api.get(this.getKey(item));
		} catch (error) {
			if(error.response)
				await this.deleteItem(this.getKey(item));
		}
	}

	store.prototype.setForceListUpdate = async function(skip) {
		return responseDB.then( async (db) => {
	    const tx = db.transaction('responses', 'readwrite');
	    const store = tx.objectStore('responses').index('store');
	    let cursor = await store.openCursor(this.config.storeName);
	    while(cursor) {
	    	if(!skip || cursor.value.id != skip)
	    		cursor.update({...cursor.value, update: true});
	    	cursor = await cursor.continue()
	    }
	  });
	}

	store.prototype.deleteUnavailable = async function(items, params) {
		if (!('indexedDB' in window)) {return null;}
		return dbPromise.then( async (db) => {
			const tx = db.transaction(this.config.storeName, 'readwrite');
			const store = tx.objectStore(this.config.storeName);
			let cursor;
	    if(this.config.index) {
	    	let indexValue = params[this.config.index] || '';
	    	indexValue = parseInt(indexValue) || indexValue;
	    	cursor = await store.index(this.config.index).openCursor(indexValue);
	    } else {
	    	cursor = await store.openCursor();
	    }
	    let item;
			while(cursor && this.satisfyFilter(cursor.value, params)) {
				if(this.isDummy(cursor.value)){
					cursor = await cursor.continue();
					continue;
				}

				item = items.find( itm => this.compareItem(itm, cursor.value) );
				if(!item) {
					await cursor.delete();
				}
				cursor = await cursor.continue();
			}
		})
	}

	store.prototype.deleteSync = async function(itemId) {
		if (!('indexedDB' in window || !syncQueueDBPromise)) {return null;}
		let requests = []
		await this.syncQueueDBPromise.then( async (db) =>{
		    let t = db.transaction('requests','readonly');
		    let o = t.objectStore('requests');
		    let i = o.index('queueName');
		    let cursor = await i.openCursor(this.config.queueName);
		    while (cursor) {
		      let req = new Request(cursor.value.url, cursor.value.requestData);
		      requests.push({request: req, id: cursor.value.id});
		      cursor = await cursor.continue();
		    }
		})
		let bgSync = await requests.find( async (r) => {
		    let req = await r.request.clone().json()
		    return req && this.compareItemWithkey(req,itemId);
		})

		if(bgSync) {
		  await this.syncQueueDBPromise.then( db => {
		    let t = db.transaction('requests', 'readwrite');
		    let o = t.objectStore('requests');
		    o.delete(bgSync.id);
		  })
		}
	}

	store.prototype.findListResponse = async function(params) {
		return await responseDB.then( async (db) => {
			let tx = db.transaction('responses', 'readonly');
			let store = tx.objectStore('responses');
			let index = store.index('store');
			let cursor = await index.openCursor(this.config.storeName);
			let field;
			while(cursor) {
				if(Object.keys(params).reduce( (b, field) => (b && ((!params[field] && !cursor.value.params[field]) || (params[field] == cursor.value.params[field]))), true))
					break;
				cursor = await cursor.continue();
			}
			return cursor ? cursor.value : null;
		})
	}

	store.prototype.formatKey = function(itemKey) {
		if(Array.isArray(itemKey)) {
			return itemKey.reduce( (keys, key) => (keys.push(parseInt(key) || key), keys), []);
		} else {
			return parseInt(itemKey) || itemKey;
		}
	}

	store.prototype.cacheItemUpdate = async function(item, error={}) {
		let itemKey = this.getKey(item);
		let itemCache = (await this.getItem(itemKey) || item);
		item = {...item, __meta_pending: true, __meta_error: error, __meta_prev: (itemCache.__meta_prev ? itemCache.__meta_prev : itemCache) }
		await this.saveItem(item);
		return item;
	}

	store.prototype.cacheItemCreate = async function(item, error = {}) {
		await this.setForceListUpdate();
		item = {...item, __meta_pending: true, __meta_error: error};
		await this.saveItem(item);
		return item;
	}

	store.prototype.compareItemArrayKey = function(item1, item2) {
		return this.config.key.reduce( (b,key) => ( b && (item1[key] == item2[key])), true );
	}

	store.prototype.compareItemSingleKey = function(item1, item2) {
		return item1[this.config.key] == item2[this.config.key];
	}

	store.prototype.compareItemWithArrayKey = function(item, itemId) {
		return this.config.key.reduce( (b, key, index) => (b && (item[key] == itemId[index])), true );
	}

	store.prototype.compareItemWithSingleKey = function(item, itemId) {
		return item[this.config.key] == itemId;
	}

	store.prototype.compareKeyArrayKey = function(itemId1, itemId2) {
		return this.config.key.reduce( (b, key, index) => (b && (itemId1[index] == itemId2[index])), true );
	}

	store.prototype.compareKeySingleKey = function(itemId1, itemId2) {
		return itemId1 == itemId2;
	}

	store.prototype.satisfySingleFilter = function(item, params) {
		return item[this.config.filter] == params[this.config.filter];
	}

	store.prototype.satisfyArrayFilter = function(item, params) {
		return this.config.filter.reduce( (b,filter) => ( b && (item[filter] == params[filter])), true );
	}

	store.prototype.insertDummyKeyArray = function(item) {
		return this.config.key.reduce( (itm, keyAttr) => (itm[keyAttr] = 'dummy_' + Date.now(),itm), item);
	}

	store.prototype.insertDummyKeySingle = function(item) {
		return ({...item, [this.config.key]: 'dummy_' + Date.now()});
	}

	store.prototype.isDummyArray = function(item) {
		return this.config.key.reduce( (b, key) => (b && String(item[key]).startsWith('dummy_')), true);
	}

	store.prototype.isDummySingle = function(item) {
		return String(item[this.config.key]).startsWith('dummy_');
	}

	store.prototype.getKeyArray = function(item) {
		return this.config.key.reduce( (k, keyAttr) => (k.push(item[keyAttr]),k), []);
	}

	store.prototype.getKeySingle = function(item) {
		return  item[this.config.key];
	}

	store.prototype.getKeyStore = function(item) {
		return [this.config.storeName, this.getKey(item)];
	}

	store.prototype.getKeyStoreFromKey= function(itemId) {
		return [this.config.storeName, itemId];
	}

	store.prototype.getItemFromKeyArray = function(itemKey) {
		return this.config.key.reduce( (itm, keyAttr, index) => (itm[keyAttr] = itemKey[index],itm), {});
	}

	store.prototype.getItemFromKeySingle = function(itemKey) {
		return ({[this.config.key]: itemKey});
	}

	store.prototype.hasKeyArray = function(item) {
		return this.config.key.reduce( (b,key) => (b && key in item), true );
	}

	store.prototype.hasKeySingle = function(item) {
		return this.config.key in item;
	}

	store.prototype.listResponseDataNestedMultiple = function(response) {
		this.listField.reduce( (data, field) => data[field], response )
	}

	store.prototype.listResponseDataNested = function(response) {
		return response[this.listField];
	}

	store.prototype.setListResponseDataNestedMultiple = function(response, data, override=true) {
		this.listField.reduce( (res, field, index, list) => {
			if( index < (list.length - 1)) {
				if(!res[field])
					res[field] = {}
				return res[field];
			}
			else{
				if(!override) {
					if(Array.isArray(res[field]))
						res[field] = [...data, ...res[field]];
				}
				else
					res[field] = data;
				return res;
			}
		}, response);
		return response;
	}

	store.prototype.setListResponseDataNested = function(response, data, override=true) {
		if(!override) {
			if(Array.isArray(res[this.listField]))
				response[this.listField] = [...data, ...response[this.listField]];
		}
		else
			response[this.listField] = data;
		return response;
	}

	store.prototype.setGetResponseDataNestedMultiple = function(response, data) {
		this.getField.reduce( (res, field, index, list) => {
			if( index < (list.length - 1)) {
				if(!res[field])
					res[field] = {}
				return res[field];
			}
			else{
				res[field] = data;
				return res;
			}
		}, response);
		return response;
	}

	store.prototype.setGetResponseDataNested = function(response, data) {
		response[this.listField] = data;
		return response;
	}

	store.prototype.getResponseDataNestedMultiple = function(response) {
		this.getField.reduce( (data, field) => data[field], response )
	}

	store.prototype.getResponseDataNested = function(response) {
		return response[this.getField];
	}
