import React, {Component} from 'react'
import { CSSTransition } from "react-transition-group";
import { Spring, animated } from "react-spring/renderprops";
import './slide-vertical.css';

export default class extends Component {
  state = {
    entering: false
  };
  onEnter = (node) => {
    setTimeout(() => this.setState({entering: true}),250);
  }
  
  onExit = () => {
    this.setState({entering: false})
  }
  render(){
    let entering = this.state.entering;
    return <CSSTransition {...this.props} classNames='slide-vertical-transition'
        timeout={{enter: 250, exit: 175}}
        onEnter={this.onEnter}
        onEntering={this.onEntering}
        onEntered={this.onEntered}
        onExit={this.onExit}
        onExiting={this.onExiting}
      >
        <Spring from={{opacity: 0, translateY: '12px'}}
          to={{opacity: entering ? 1 : 0, translateY: entering ? '0' : '12px' }}>

          {({opacity, translateY}) => (
            <animated.div className='slide-vertical-transition'
              style={{opacity: `${opacity}`, transform: `translate3d(0,${translateY},0)`}}>
              {this.props.children}
            </animated.div>
          )}
        </Spring>

      </CSSTransition>
  }
}
