import axios from 'axios';

export default class Api {
	list(params) {
		return axios.get('/api/examination/records', {params: {...params}});
	}

	get(recordId) {
		return axios.get(`/api/examination/records/${recordId[0]}/${recordId[1]}`);
	}

	update(data) {
		return axios.post(`/api/examination/records` ,data);
	}

	create(data) {
		return axios.post('/api/examination/records' ,data);
	}

	delete(recordId) {
		return axios.delete(`/api/examination/records/${recordId[0]}/${recordId[1]}`);
	}
}