import axios from 'axios';

export default class Api {
	list() {
		return axios.get('/api/examination/groups');
	}

	get(id) {
		return axios.get(`/api/examination/groups/${id}`);
	}

	update(data) {
		return axios.put(`/api/examination/groups/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/examination/groups' ,data);
	}

	delete(id) {
		return axios.delete(`/api/examination/groups/${id}`)
	}
}