import React, {Component, Suspense, lazy} from 'react';
import {BrowserRouter as Router,Route,Redirect,Link} from 'react-router-dom';
import {Provider} from 'mobx-react'
import axios from 'axios';

import Navbar from './components/Navbar';
import Footer from './components/Footer';
import AnimatedSwitch from './components/animated-switch';
import {FadeTransition} from './components/transitions';
import Loading from './components/Loading';
import LoadingFailed from './components/LoadingFailed';

import './sass/app.scss';

import {userStore, studentStore, reportStore, classesStore, subjectStore, subjectGroupStore, examGroupStore, examStore, recordStore, performanceStore,
	analysisStore, teacherStore, teacherDutyStore, teacherAssessmentStore, staffStore, staffOffdayStore} from './store';


const Login = lazy(() => import('./pages/Login'));
const Register = lazy(() => import('./pages/Register'));
const Auth = lazy(() => import('./pages/Auth'));
const Admin = lazy(() => import('./pages/Admin'));
const PayFees = lazy(() => import('./pages/PayFees'));

export default class App extends Component {
	state = {loading: true};
	componentDidMount(){
		userStore.get().then(user=>{}, error=> {}).finally( ()=>this.setState({loading: false}) )
	}
	
	render() {
		return <Provider 
			userStore={userStore}
			studentStore={studentStore}
			reportStore={reportStore}
			classesStore={classesStore}
			subjectStore={subjectStore}
			subjectGroupStore={subjectGroupStore}
			examGroupStore={examGroupStore}
			examStore={examStore}
			recordStore={recordStore}
			performanceStore={performanceStore}
			analysisStore={analysisStore}
			teacherStore={teacherStore}
			teacherDutyStore={teacherDutyStore}
			teacherAssessmentStore={teacherAssessmentStore}
			staffStore={staffStore}
			staffOffdayStore={staffOffdayStore}>
			<Router>
				<FadeTransition in={!this.state.loading}>
					<LoadingFailed>
						<Suspense fallback={<Loading />}>
							<AnimatedSwitch>
								<Route path="/pay-fees" render={(props) => <PayFees {...props} /> } />
								<Route path="/login" render={(props) => <Auth {...props} />} />
								<Route path="/register" render={(props) => <Auth {...props} />} />
								<Route path="/school" render={(props) => <Admin {...props} /> } />
								<Route path=""> <Redirect to="/school" /> </Route>
							</AnimatedSwitch>
						</Suspense>
					</LoadingFailed>
				</FadeTransition>
				<FadeTransition in={this.state.loading}>
					<Loading />
				</FadeTransition>
			</Router>
		</Provider>
	}
};