import '../libs/bootstrap-notify';

//types = ['primary', 'info', 'success', 'warning', 'danger'];
export default function(message, type='primary', icon = "nc-app", from='top', align='right') {
  $.notify({
      icon: `nc-icon ${icon}`,
      message: message
  }, {
      type: type,
      timer: 8000,
      placement: {
          from: from,
          align: align
      }
  });
}