import axios from 'axios';

export default class Api {
	list() {
		return axios.get('/api/setup/subject-groups');
	}

	get(id) {
		return axios.get(`/api/setup/subject-groups/${id}`);
	}

	update(data) {
		return axios.put(`/api/setup/subject-groups/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/setup/subject-groups' ,data);
	}

	delete(id) {
		return axios.delete(`/api/setup/subject-groups/${id}`);
	}
}