import axios from 'axios';

export default class Api {
	list() {
		return axios.get('/api/setup/classes');
	}

	get(id) {
		return axios.get(`/api/setup/classes/${id}`);
	}

	update(data) {
		return axios.put(`/api/setup/classes/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/setup/classes' ,data);
	}

	delete(id) {
		return axios.delete(`/api/setup/classes/${id}`)
	}
}