import axios from 'axios';

export default class Api {
	list() {
		return axios.get('/api/setup/subjects');
	}

	get(id) {
		return axios.get(`/api/setup/subjects/${id}`);
	}

	update(data) {
		return axios.put(`/api/setup/subjects/${data.id}` ,data);
	}

	create(data) {
		return axios.post('/api/setup/subjects' ,data);
	}

	delete(id) {
		return axios.delete(`/api/setup/subjects/${id}`);
	}
}